"use client";

import { useState } from "react";

import {
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  Input,
  Button,
  Box,
  Text,
} from "@chakra-ui/react";

function page() {
  const [month, setMonth] = useState<number>(11);
  const [year, setYear] = useState<number>(2025);
  const [data, setData] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);

  const fetchFitnessData = async () => {
    if (!month || !year) {
      alert("Please enter both month and year.");
      return;
    }

    setLoading(true);
    setData([]);

    try {
      const url = `http://localhost:8080/api/v1/fetch/fetchfitnessdata?month=${month}&year=${year}`;

      const res = await fetch(url, {
        method: "GET",
        credentials: "include",
      });

      const result = await res.json();

      if (res.ok) {
        setData(result.data || []);
      } else {
        alert(result.message || "Failed to fetch data");
      }
    } catch (error) {
      console.error(error);
      alert("Something went wrong");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box bg="#0f0f0f" color="white" minH="100vh" py={10} px={4}>
      <Box w="100%" bg="black" p={6} rounded="lg" shadow="md">
  <Table size="sm" variant="striped" colorScheme="blackAlpha" w="100%" tableLayout="fixed">
    <Thead>
      <Tr>
        {Object.keys(data[0] || {}).map((key) => (
          <Th key={key}>{key}</Th>
        ))}
      </Tr>
    </Thead>
    <Tbody>
      {data.map((item, idx) => (
        <Tr key={idx}>
          {Object.entries(item).map(([key, value]) => (
            <Td
              key={key}
              color="white"
              wordBreak="break-word"
              whiteSpace="normal"
              fontSize="sm"
            >
              {key === "imageUrl" && value ? (
                <a
                  href={value as string}
                  target="_blank"
                  rel="noopener noreferrer"
                  style={{ color: "#60A5FA", textDecoration: "underline" }}
                >
                  RC BOOK IMAGE
                </a>
              ) : (
                value?.toString()
              )}
            </Td>
          ))}
        </Tr>
      ))}
    </Tbody>
  </Table>
</Box>

    </Box>
  );
}

export default page
